body {
  font-family: sans-serif;
  height: 300rem;
}

:root {
  /* --purple
  --silver
  --light-purple
  --gray */
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-padding-top: 7rem;
  scroll-behavior: smooth;
}

* {
  /*   font-family: */
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

section {
  padding: 2rem 9%;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 3rem 9%; /* change these values */
}

.header .nav a {
  font-size: 1.6rem;
  margin: 1rem;
  color: black;
}

.logoPic {
  height: 4.71rem;
}

.header .nav a:hover {
  /* color: purple; */
}

.btn {
  display: inline-block;
  margin-top: 1rem;
  border-radius: 0.5rem;
  /*   background: purple;
  color: black;  change these values */
  font-weight: 600;
  font-size: 1.6rem;
  cursor: pointer;
  padding: 0.8rem 3rem;
}

.btn:hover {
  /* background: change color here*/
}

#hamburger {
  font-size: 2.5rem;
  color: gray;
  cursor: pointer;
  display: none;
}

.header.active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  padding: 2rem 9%; /*change*/
}

.custom-shape-divider-bottom-1669232475 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.custom-shape-divider-bottom-1669232475 svg {
  position: relative;
  display: block;
  width: calc(100% + 1.3px);
  height: 110px;
  transform: rotateY(180deg);
}

.custom-shape-divider-bottom-1669232475 .shape-fill {
  fill: #d8d8d8;
}
/* responsive design */

@media (max-width: 1024px) {
  :root {
    font-size: 55%;
  }
  .header {
    padding: 2rem;
  }
  .header.active {
    padding: 2rem;
  }

  section {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
  :root {
    font-size: 55%;
  }

  #hamburger {
    display: block;
  }

  .header .nav {
    position: absolute;
    top: 99%; /* shift the navbar right below the header when resolution is shrunk */
    left: 0;
    right: 0;
    background: white;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.1);
    clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }

  .header .nav.active {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }

  .header .nav a {
    display: block;
    margin: 2rem;
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  :root {
    font-size: 50%;
  }
}

.home {
  padding-top: 10rem;
  text-align: center;
  overflow-x: hidden;
}

.home h1 {
  font-size: 7.5vw;
  text-transform: uppercase;
  color: black;
}

.home img {
  width: 100%;
  margin: 1rem 0;
}

.heading {
  text-align: center;
  padding-bottom: 2rem;
  font-size: 4.5rem;
  color: black;
}
